<template>
  <div class="beijinTY">
   <fg-header title="竞技场"></fg-header>
   <div class="fangjian">
        <div class="fuzhichenggong" @click="room('add')">
          <h1 class="chenggongtitle">创建房间</h1>
        </div>
         <div class="fuzhichenggong" @click="room('join')">
          <h1 class="chenggongtitle">加入房间</h1>
        </div>
   </div>
 
   <div class="guantouqiang">
      <div class="lateral-sliding">
        <ul class="lateral-sliding-item">
          <li class="pk" v-for="item in listAll" :key="item.id" @click="goToroom('大厅',item)">
            <div class="pknum">
              <div class="pkbor">{{item.player_amount}}人PK</div>
            </div>
            <img class="GTQimg" :src="guantouqingSrc" alt="">
            <div class="clearfix pkfoot">
              <span class="fl">{{item.usdt_amount}}GTF</span>
              <span class="fr">{{item.waiter_amount}}人</span>
            </div>
          </li>
        </ul>
      </div>
   </div>
  
   <div>
     <ul class="marT50 listul">
       <li class="listLi" v-for="item in gameslist" :key='item.id'>
         <div class="jilu">
           <div class="jilutext clearfix">
             <p>{{item.usdt_amount}}GTF N人</p>
             <p>PK成功   获得***GTF</p>
             <p>xxxx-xx-xx xx:xx:xx</p>
           </div>
         </div>
       </li>
     </ul>
   </div>
    <van-popup v-model="popupshow">
        <div class="yaoqingbg" >
          <h1 class="yaoqingtitle">{{roomtype}}</h1>
            <ul class="copy_text" v-if="roomtype=='创建房间'">
              <li>
                <span>PK数额</span>
                <input type="text" v-model="usdt_amount" class="inputStyle">
              </li>
              <li>
                <span>最低账户余额</span>
                <input type="text" v-model="min_usdt_amount" class="inputStyle">
              </li>
              <li>
                <span>满员人数</span>
                <input type="text" v-model="player_amount" class="inputStyle">
              </li>
              <li>
                <span>PK成功人数</span>
                <input type="text" v-model="winner_amount" class="inputStyle">
              </li>
              <li>
                <span>房间密码</span>
                <input type="text" v-model="password" class="inputStyle">
              </li>
            </ul>
            <ul class="copy_text" v-else>
              <li>
                <span>房间ID</span>
                <input type="text" v-model="Jid" class="inputStyle">
              </li>
              <li>
                <span>房间密码</span>
                <input type="text" v-model="Jpassword" class="inputStyle">
              </li>
            </ul>
          <div class="btnline">
            <div class="quxiao" @click="popupshow=false">取消创建</div>
            <div v-if="roomtype=='创建房间'" class='fuzhi' @click="goToroom('创建')">马上购买</div>
            <div v-else class='fuzhi' @click="goToroom('加入')">马上加入</div>
          </div>
        </div>
      </van-popup>
  </div>
</template>
<script type='text/ecmascript-6'>
import { gameslist_all, gameslist_my,gamesroomlist_by_room,game_roomscreate,gamesquery ,entered_room,gamessearch} from '@/api/pk'

export default {
  data() {
    return {
      guantouqingSrc:require('../../assets/images/suancui.png'),
       popupshow:false,
       value:'',
        drag:false,
        roomtype:'创建房间',
      listAll:[],
      usdt_amount:'',
      player_amount:'',
      min_usdt_amount:'',
      winner_amount:'',
      password:'',
      Jpassword:'',
      Jid:'',
      gameslist:[]
    };
  },
  created() {
  },
  mounted() {
     this.gameslist_all()
     this.listjilu()
     this.entered_room()
  },
  methods: {
    listjilu(){

      gameslist_my().then((res) => {
          gamesroomlist_by_room().then((resA) => {
           this.gameslist=res.data.data.concat(resA.data.data)
          }).catch((errora) => {reject(errora)})
      }).catch((error) => {reject(error)})
     
    },
    entered_room(){
      entered_room().then((res) => {
          if(res.data.data.entered==true){
             this.$router.push({ path:'/room',query:{id:res.data.data.game_room_id,type:'加入房间'}})
          }
          }).catch((error) => {
            reject(error)
          })
    },
    goToroom(type,item){
         if( type=='创建'){
           if(this.player_amount<2){
               this.$toast('满员人数不能小于2')
              return
            }
            if(this.winner_amount<1){
               this.$toast('PK成功人数不能小于1')
              return
            }
            if(this.min_usdt_amount<=this.usdt_amount){
               this.$toast('最低账户余额不能小于PK数额')
              return
            }
         
           this.game_roomscreate(type)
         }
         if(type=='加入'){
            let data={
           id:this.Jid,
           password:this.Jpassword,
          }
          gamessearch(data).then((res) => {
            this.$router.push({ path:'/room',query:{id:this.Jid,type:type}})
            }).catch((error) => {
              reject(error)
            })
         }
        if(type=='大厅'){
           this.$router.push({ path:'/room',query:{id:item.id,type:type}})
         }
     
         

    },
    room(type){
      this.popupshow=true
      if(type=='add'){
        this.roomtype='创建房间'
      }else{
        this.roomtype='加入房间'
      }
      
    },
     gameslist_all(){
       gameslist_all().then((res) => {
        this.listAll=res.data.data
          }).catch((error) => {
            reject(error)
          })
     },
     game_roomscreate(type){
       let data={
          usdt_amount:this.usdt_amount,
          player_amount:this.player_amount,
          min_usdt_amount:this.min_usdt_amount,
          winner_amount:this.winner_amount,
          password:this.password,
          ...this.$datapublic
       }
       game_roomscreate(data).then((res) => {
          this.$router.push({ path:'/room',query:{id:res.data.data.id,type:type}})
          }).catch((error) => {
            reject(error)
          })
     }
   
  },
  filters: {
       
   },
};
</script>
